<template>
	<div class="user">
		<div class="user-nav">
			<div class="in-nav">
				<img src="../../assets/img/logo2.png" alt="" @click="jump" style="cursor: pointer;">
				<el-dropdown>
					<span class="el-dropdown-link">
						<span class="user-name">用户中心<i class="user-info" v-show="ishow"></i></span><i class="el-icon-arrow-down el-icon-caret-bottom"></i>
					</span>
					<el-dropdown-menu slot="dropdown" trigger="click">
						<el-dropdown-item @click.native="gotoUser()">个人设置</el-dropdown-item>
						<el-dropdown-item @click.native="toinf()"><span class="user-name">我的消息<i class="user-info" v-show="ishow"></i></span></el-dropdown-item>
						<el-dropdown-item @click.native="tocol()">我的关注</el-dropdown-item>
						<el-dropdown-item @click.native="amend()">修改密码</el-dropdown-item>
						<el-dropdown-item @click.native="order()">我的订单</el-dropdown-item>
						<el-dropdown-item @click.native="quit()">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>
		<div class="user-text">
			<div class="text-nav">
				<div class='user-news'>
					<el-upload action="http://pre.biaodaa.com/userCenter/updateHeadPortrait" :on-success="upLoadFn" auto-upload
					 :show-file-list="false">
						<template v-if="userData.imageUrl">
							<img :src="userData.imageUrl" alt="">
						</template>
						<template v-else>
							<img src="../../assets/img/pic-toux.png@2x.png" alt="">
						</template>
					</el-upload>

					<div class="user-first">
						<div class="user-name left">
							{{userData.nikeName}}
						</div>
						<div class="user-state left">{{state}}
						</div>
					</div>
					<div class="user-day">
						剩余天数: <span class="p-color">{{day}}</span>天
					</div>
					<div class="user-btn" @click="jumpapp">{{vipname}}
					</div>
				</div>
				<div class="user-tab">
					<div class="tab" v-for="(el,i) in tab" :key="i" :class="el.i ? 'current' : '' " @click='jumpto(el)'>
						{{el.name}}
						<div class="nav-rim" v-show="el.i">
							<div class="triangle">

							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="text-right">
				<router-view @refresh='rename' />
			</div>
		</div>
	</div>
</template>
<script>
	import {
		getUserTemp,
		Cmessage
	} from '@/api/index'
	export default {
		data() {
			return {
				tab: [{
						name: '个人信息',
						to: 'pDet',
						i: true
					},
					{
						name: '我的消息',
						to: 'info',
						i: false
					},
					{
						name: '我的关注',
						to: 'fcoll',
						i: false
					}, {
						name: '修改密码',
						to: 'root',
						i: false
					}, {
						name: '我的订单',
						to: 'order',
						i: false
					}, {
						name: '退出登录',
						i: false
					}
				],
				name: '个人信息',
				userData: {},
				day: 0,
				state: '非会员',
				vipname: '开通会员',
				ishow: false
			}
		},
		methods: {
			upLoadFn(e) {
				this.userData.imageUrl = e.imgPath;
				let that = this;
				this.$http({
					method: 'post',
					url: '/userCenter/updateUserInfo',
					data: this.userData
				}).then(function(res) {
					if (res.data.code == 1) {
						that.$notify({
							title: '提示',
							message: '用户信息更新成功',
							offset: 100
						});

					}
				})
			},
			rename(val) {
				this.userData.nikeName = localStorage.getItem('Bname')
			},
			jump() {
				this.$router.push('/')
			},
			getUser() {
				let that = this;
				getUserTemp({}).then(res => {
					if (res.code == 1) {
						this.userData = res.data
						let gap = new Date(this.userData.expiredDate).getTime() - new Date().getTime()
						if (gap >= 0) {
							that.day = Math.ceil(gap / 3600 / 24 / 1000)
						} else {
							that.day = 1
						}
						//  this.day = Math.ceil(gap/3600/24/1000) >= 0 ? Math.ceil(gap/3600/24/1000) : 0;
						if (this.userData.roleName == '会员用户') {
							this.state = '会员'
							this.vipname = '会员续费'
						} else {
							this.state = '非会员'
							this.vipname = '开通会员'
							that.day = 0
						}

					}
				})
			},
			jumpto(el) {
				if (el.name == '退出登录') {
					sessionStorage.removeItem('xtoken')
					localStorage.removeItem('Bname')
					localStorage.removeItem('Xtoken')
					localStorage.removeItem('isvip')
					this.$router.push('/home')
				} else {
					this.name = el.name
					this.tab.forEach(li => {
						li.i = false
					})
					el.i = true
					this.$router.push({
						path: el.to
					})
				}
			},
			jumpapp() {
				this.$router.push('/buy')
			},
			amend() {
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[3].i = true;
				this.$router.push('/user/root')
			},
			gotoUser() {
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[0].i = true;
				this.$router.push('/user/pDet')
			},
			tocol() {
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[2].i = true;
				this.$router.push('/user/fcoll')

			},
			toinf() {
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[1].i = true;
				this.$router.push('/user/info')
			},
			order() {
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[4].i = true;
				this.$router.push('/user/order')
			},
			quit() {
				sessionStorage.removeItem('xtoken')
				localStorage.removeItem('Bname')
				localStorage.removeItem('Xtoken')
				localStorage.removeItem('valid')
				localStorage.removeItem('isvip')
				this.$router.push('/home')
			},
			isroot() {
				this.name = this.tab[this.$route.meta.i].name
				this.tab.forEach(li => {
					li.i = false
				})
				this.tab[this.$route.meta.i].i = true
			},
			gainCo() {
				Cmessage({}).then(res => {
					if (res.code == 1) {
						if (res.data >= 1) {
							this.ishow = true
						} else {
							this.ishow = false
						}

					}
				})
			}
		},
		watch: {
			$route: {
				handler: function(val, oldVal) {
					this.gainCo()
				},
				deep: true
			}
		},
		created() {
			this.isroot()
			this.getUser()
			this.gainCo()
		},
		components: {}
	}
</script>
<style lang="less" scoped>
	.user {
		width: 100%;
		background-color: #fafafa;

		//  padding-top: 100px;
		.user-nav {
			width: 100%;
			height: 60px;
			margin-bottom: 16px;
			background-color: #fff;

			.in-nav {
				width: 1020px;
				margin: 0 auto;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
		}

		.user-text {
			width: 1020px;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;

			.text-nav {
				.user-news {
					width: 262px;
					height: 300px;
					background-color: #fff;
					display: flex;
					align-items: center;
					flex-direction: column;
					margin-bottom: 16px;
					padding-top: 21px;

					img {
						border-radius: 50%;
						width: 67px;
						height: 67px;
					}
				}

				.user-first {
					margin-top: 16px;
					margin-bottom: 14px;
				}

				.user-name {
					max-width: 180px;
					font-size: 18px;
					font-weight: 500;
					margin-right: 14px;
					text-align: center;
					text-overflow: ellipsis;
					overflow: hidden;
					white-space: nowrap;
				}

				.user-state {
					padding: 0 10px;
					line-height: 20px;
					background-color: #E4FFF0;
					font-size: 10px;
					color: #53E493;
				}

				.user-day {
					width: 100%;
					font-size: 12px;
					color: #999;
					text-align: center;
					box-sizing: border-box;
					margin-bottom: 60px;
				}

				.user-btn {
					width: 106px;
					height: 37px;
					border: 1px solid #FE6603;
					border-radius: 8px;
					text-align: center;
					line-height: 37px;
					font-size: 16px;
					color: #FE6603;
					font-weight: 550;
					cursor: pointer;
				}

				.user-tab {
					width: 262px;
					font-size: 14px;
					margin-bottom: 150px;

					.tab {
						width: 262px;
						height: 50px;
						line-height: 50px;
						text-align: center;
						font-weight: 550;
						background-color: #fff;
						position: relative;
						cursor: pointer;

						.nav-rim {
							position: absolute;
							width: 10px;
							height: 54px;
							right: -10px;
							top: 0;
							background-color: #FE6603;

							.triangle {
								width: 0;
								height: 0;
								border-top: 6px solid #FE6603;
								border-right: 10px solid transparent;
								position: absolute;
								right: 0;
								bottom: -6px;
							}
						}
					}

					.current {
						background-color: #FE6603;
						color: #fff;
					}
				}
			}

			.text-right {
				width: 100%;
				margin-left: 29px;
				margin-bottom: 150px;
			}
		}

		.user-name {
			position: relative;

			.user-info {
				position: absolute;
				right: -6px;
				top: -2px;
				border: 1px solid red;
				border-radius: 50%;
				width: 5px;
				height: 5px;
				background-color: red;
			}
		}
	}
</style>



